/*
 * Pojoaque Style by Jason Tate
 * http://web-cms-designs.com/ftopict-10-pojoaque-style-for-highlight-js-code-highlighter.html
 * Based on Solarized Style from http://ethanschoonover.com/solarized
 * http://softwaremaniacs.org/media/soft/highlight/test.html
*/

code[class*="language-"],
pre[class*="language-"] {
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
    white-space: pre;
    white-space: pre-wrap;
    word-break: break-all;
    word-wrap: break-word;
    font-family: Menlo, Monaco, "Courier New", monospace;
    font-size: 15px;
    line-height: 1.5;
    color: #dccf8f;
    text-shadow: 0;
}

pre[class*="language-"],
:not(pre) > code[class*="language-"] {
    border-radius: 5px;
    border: 1px solid #000;
    color: #DCCF8F;
    background: #181914 url('data:image/jpeg;base64,') repeat left top;
}

pre[class*="language-"] {
    padding: 12px;
    overflow: auto;
}

:not(pre) > code[class*="language-"] {
    padding: 2px 6px;
}

.token.namespace {
    opacity: .7;
}
.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
    color: #586e75;
    font-style: italic;
}
.token.number,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
    color: #468966;
}

.token.attr-name {
    color: #b89859;
}
.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
    color: #dccf8f;
}
.token.selector,
.token.regex {
    color: #859900;
}
.token.atrule,
.token.keyword {
    color: #cb4b16;
}

.token.attr-value {
    color:  #468966;
}
.token.function,
.token.variable,
.token.placeholder {
    color:  #b58900;
}
.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol {
    color: #b89859;
}
.token.tag {
    color: #ffb03b;
}
.token.important,
.token.statement,
.token.deleted {
    color: #dc322f;
}
.token.punctuation {
    color:  #dccf8f;
}
.token.entity {
    cursor: help;
}
.token.bold {
    font-weight: bold;
}
.token.italic {
    font-style: italic;
}

